import React from 'react';
import { useEffect } from 'react';
import "./Contact.css";

const Contact = () => {

    useEffect(() => {
        document.getElementById('contact').classList.add('toggle')
    }, [])

    return (
        <div className='contact'>
            <p>Email: dawnmerch@gmail.com</p>
            <p>Phone: 9999999999</p>
        </div>
    )
}

export default Contact
