import React from 'react';
import { useEffect } from 'react';
import "./About.css";

const About = () => {

    useEffect(() => {
        document.getElementById('about').classList.add('toggle')
    }, [])

    return (
        <div className='about'>
            Dawn Merch is an e-commerce platform where users can buy different products. This is a practice project and currently does not offer real-time services.
        </div>
    )
}

export default About
